@import url('https://fonts.googleapis.com/css?family=Do+Hyeon:400');
@import url('https://fonts.googleapis.com/css?family=Poor+Story:400');


* {margin:0; padding:0;}

body {
  /* background: #22cc88; */
  padding: 0;
  margin: 0;
  font-family: "Noto Sans KR", sans-serif;
}

#root {
}

.KolbApp {
}

.mb10 {margin-bottom: 10px;}
.mt20 {margin-top: 20px;}
.mt50 {margin-top: 50px;}
.mb20 {margin-bottom: 20px;}
.mb40 {margin-bottom: 40px;}
.pa10 {padding: 10px;}
.pa20 {padding: 20px;}
.my20 {margin: 20px 0;}

.KolbApp .w-100 { width: 100%;}
.KolbApp .w-20 { width: 20%;}
.KolbApp .w-40 { width: 40%;}

.KolbApp .resultBox {
  position: relative;
  top:0;
  left:0;
  padding-bottom:20px;
  margin-bottom:20px;
  border-bottom:1px solid #ccc;
  z-index: 1;
}

.KolbApp .resultBox:last-child {
  border-bottom:none;
}


.KolbApp ul, 
.KolbApp li {
  list-style: none;
  padding: 0;
  margin: 0;
  font-weight: 500;
  font-size: 1rem;
}

.KolbApp ul {
  position: relative;
  width: 100%;
}

.KolbApp ul li {
  border-radius: 10px;
  margin-bottom: 10px;
  width: 100%;
  box-sizing: border-box;
  padding: 15px 18px;
  color: #fff;
  position: relative;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  cursor: grab;
  margin-bottom: 20px;
}

/* li:first-child {
  background-color: #FF008C;
}
li:nth-child(2) {
  background-color: #D309E1;
}
li:nth-child(3) {
  background-color: #9C1AFF;
}
li:last-child {
  background-color: #7700FF;
} */

.KolbApp li svg {
  width: 18px;
  height: 18px;
  cursor: grab;
}

.KolbApp .kolb-button {
  width: 100%;
  height: 50px;
  border: none;
  background-color: hsl(206, 100%, 52%);
  color: #fff;
  font-size: 1rem;
  border-radius: 8px;
  box-sizing: border-box;
}

.KolbApp .kolb-button:disabled {
  background-color: #eaeaea;
  cursor: default;
}

.KolbApp .kolb-button.outline {
  background-color: #fff;
  color: hsl(206, 100%, 52%);
  border: 1px solid hsl(206, 100%, 52%);
}

.KolbApp .kolb-button.shareIconButton {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 60px;
  padding: 10px;
  border-radius: 9999px;
  margin-right: 20px;
  box-sizing: border-box;
  margin-bottom: 50px;;
}

.KolbApp .kolb-button.shareIconButton:last-child {
  margin-right: 0;
}

.KolbApp .kolb-button.shareIconButton img {
  
  width:100%;
}

.KolbApp .kolb-button.shareIconButton.kakaoBtn {
  background-color: transparent;
  border: 1px solid hsl(206, 100%, 52%);
}

.KolbApp .kolb-button.shareIconButton.shareUrl img {
  
}

.KolbApp .screen.quizPage {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.KolbApp .quizTitleArea {
  display: flex;
  margin-bottom: 20px;
}

.KolbApp .quizTitleArea .numbering {
  /* width: 50%; */
  height: 95px;
  margin-right: 10px;
}

.KolbApp .quizTitleArea .numbering .numImg {
  /* width: 100%; */
  height: 100%;
}

.KolbApp .quizTitleArea .quizNo {
  position: absolute;
  top: 25px; /*7px;*/
  left: 101px; /*82px;*/
  color: #fff;
  font-size: 1.7rem;
  text-align: center;
}

.KolbApp .quizTitleArea .quizNo small {
  font-size: .9rem;
}

.KolbApp .quizTitleArea .bubble {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  padding: 10px 0;
  border: solid 2px #3d3935;
  border-radius: 6px;
  background-color: #fff;
  color: #3d3935;
  font-size: 1rem;
  text-align: center;
}

.KolbApp .quizTitleArea .bubble:before {
  position: absolute;
  top: 30px;
  left: -21px;
  width: 0px;
  height: 0px;
  border-top: 10px solid transparent;
  border-right: 10px solid #3d3935;
  border-bottom: 10px solid transparent;
  border-left: 10px solid transparent;
  content: "";
}

.KolbApp .quizTitleArea .bubble:after {
  position: absolute;
  width: 0px;
  height: 0px;
  top: 32px;
  left: -16px;
  border-top: 8px solid transparent;
  border-right: 8px solid #fff;
  border-bottom: 8px solid transparent;
  border-left: 8px solid transparent;
  content: "";
}

.KolbApp .screen {
  position: relative;
  top:0;
  left:0;
}

.KolbApp .screen.intro .selectText {
  color: #777;
  margin-bottom: 5px;
  font-size: .9rem;
}

.KolbApp .screen.intro .introImage {
  width:100%;
}


.KolbApp .screen.result  {

}

.KolbApp .screen.resultAll {
  position: relative;
  top:0;
  left:0;
}

.KolbApp .tab {
  display: flex;
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  z-index: 1000;
}

.KolbApp .tab .menu {
  position: relative;
  top:0;
  left:0;
  width: 25%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
}

.KolbApp .tab .menu:nth-child(even) {
  background-color: pink;
}
.KolbApp .tab .menu:nth-child(odd) {
  background-color:#fff;
}

.KolbApp .screen.resultAll .resultAllImageContainer {
  text-align: center;
}

.KolbApp .screen.resultAll .resultAllImage {
  position: relative;
  top:0;
  left:0;
  width: 105%;
  max-width: 600px;
  margin-left: -10px;
  margin-right: -10px;
  margin-top:10px;
  margin-bottom: 30px;
}

.KolbApp .text-center {text-align: center;}




.KolbApp .show {height: 100vh;overflow:hidden;}
.KolbApp .hide {height:0;overflow:hidden;}
.KolbApp .d-block {display: block !important;}
.KolbApp .d-none {display: none !important;}
.KolbApp .page.show {padding:20px;}
.KolbApp .page.hide {padding:0;}

.KolbApp .scoreLabel {
  display: inline-block;
  font-size: .7rem;
  width: 1.1rem;
  height: 1.1rem;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 99px;
  margin-right: 10px;;
}


/******************* Select Start ******************/

.KolbApp .kolbSelect {
  position: relative;
  top:0;left:0;
  width: 100%;
  height: 40px;
  padding: 0 10px;
}

.KolbApp .kolbSelect option {
  position: relative;
  top:0;left:0;
  width: 100%;
  height: 40px;
 
}

/******************* Select End ******************/




/******************* Bubble Start ******************/

.KolbApp .bubbleEffectPanel {
  position: fixed;
  top:0;
  left:0;
}

.KolbApp .bubbleEffectPanel .bubble {
  position: absolute;
  top:0;
  left:0;
  border-radius: 9999px;
  text-align: center;
  font-size: 2rem;
}

/******************* Bubble End ******************/




/******************* Page Start ******************/

.KolbApp .page {
  position: absolute;
  top:0;
  left:0;
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  padding: 20px;
}

.KolbApp .quizNo {
  margin-bottom: 10px;
}

.KolbApp .infoText {
  margin-bottom: 20px;
}

.KolbApp .title {
  font-size: 1.8rem;
  margin-bottom: 20px;
  text-align: center;
  font-family: "Do Hyeon";
}

/******************** Page End *******************/



/******************** ResultPage Start *******************/

.KolbApp .resultChart {
  position: relative;
  top:0;
  left:0;
  margin-bottom: 20px;
  text-align: center;
  margin-left: -10px;
  margin-right: -10px;
}

.KolbApp .resultChart .label {
  position: absolute;
  width: 125px;
  height: 18px;
  background-color: red;
  color: #fff;
  font-size: .7rem;
}


.KolbApp .resultChart .label.accommodator {
  top:13px;
  left:35px;
  background-color: #88CDF6;
}

.KolbApp .resultChart .label.diverger {
  top:13px;
  right:12px;
  background-color: #61A7DA;
}

.KolbApp .resultChart .label.converger {
  top: 35vw;
  left: 35px;
  background-color: #3E84B8;
}

.KolbApp .resultChart .label.assimilator {
  top: 35vw;
  right: 12px;
  background-color: #1F5D96;
}


.KolbApp .resultImg {
  width: 100%;
  margin-bottom: 20px;
}


/******************** ResultPage End *******************/


/******************** Result Start *******************/

.KolbApp .kolbType {
  position: relative;
  top:0;
  left:0;
}

.KolbApp .kolbType .mainImage {
  position: relative;
  top:0;
  left: 0;
  width: 100%;
  height: 1vw;
  display: block;
  height: 300px;
  margin-bottom: 20px;
}

.KolbApp .kolbType .mainImage .titleArea {
  text-align: center;
  color: #fff;
  font-weight: normal;
  border-radius: 20px;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.KolbApp .kolbType .mainImage .titleArea .titleImg {
  height: 90px;
  width: auto;
}

.KolbApp .kolbType .mainImage .charArea {
  position: relative;
  top:0;
  left:0;
  display: flex;
  width: 100%;
  height: 160px;
}

.KolbApp .kolbType .mainImage .charArea .charImg {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 150px;
}

.KolbApp .kolbType .mainImage .charArea .charImg.Diverger {
  height: 120px;
}

.KolbApp .kolbType .mainImage .charArea .charImg.Assimilator {
  height: 130px;
}

.KolbApp .kolbType .mainImage .titleArea.Accommodator {
  background-color: #88cdf6;
}
.KolbApp .kolbType .mainImage .titleArea.Diverger {
  background-color: #61a7da;
}
.KolbApp .kolbType .mainImage .titleArea.Converger {
  background-color: #3e84b8;
}
.KolbApp .kolbType .mainImage .titleArea.Assimilator {
  background-color: #1f5d96;
}

.KolbApp .kolbType .mainImage .titleArea .typeTitle {
  font-size: 2rem;
}

.KolbApp .kolbType .mainImage .titleArea .typeText {
  font-size: 1.2rem;
}

.KolbApp .kolbType .mainImage .titleArea .typeText span {
  
}

.KolbApp .kolbType .mainImage ul.spec {
  margin-top: 20px;
  padding-left: 10px;
}

.KolbApp .kolbType .mainImage ul.spec li{
  border-radius: none;
  margin-bottom: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0;
  color: #000;
  position: relative;
  align-items: left;
  flex-shrink: 0;
  cursor: pointer;
  font-family: "Do Hyeon";
  font-size: 1rem;
}




.KolbApp .kolbType .mainImage ul.spec li::before {
  position: absolute;
  content:'*';
  top: 0;
  left: -10px;
}

.KolbApp .kolbType .mainImage .tags {
  color: #1F5D96;
  font-size: .9rem;
}



.KolbApp .kolbType .mainImage .resultImg {
  position: absolute;
  top:0;
  left: 0;
  z-index: -1;
}

.KolbApp .kolbType .subTitle {
  margin-top: 20px;
}

.KolbApp .kolbType ul.dot {
  padding-left: 10px;
}

.KolbApp .kolbType ul.dot li{
  border-radius: none;
  margin-bottom: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0;
  color: #000;
  position: relative;
  align-items: left;
  flex-shrink: 0;
  cursor: pointer;
  font-weight: normal;
}

.KolbApp .kolbType ul.dot li::before {
  position: absolute;
  content:'*';
  top: 3px;
  left: -10px;
}

.KolbApp .kolbType h3 {
  display: flex;
  align-items: flex-end;
  padding:0;
  margin-bottom: 5px;
  /* font-family: "Do Hyeon"; */
}

.KolbApp .kolbType h3 h1 {
  position: relative;
  top:7px;
}

.KolbApp .kolbType .tag {
  display: block;
  color:#777;
  margin-bottom: 10px;
  background-color: rgb(255, 255, 223);
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid rgb(255, 244, 97);
}

.KolbApp .kolbType small span {
  color:#61A7DA;
}

.KolbApp .kolbType .text {
  position: relative;
  top:0;
  line-height: 1.5;
}

/******************** Result End *******************/
